@import '../../../imports';

.line-base-chart {
  position: relative;

  .event-region {
    position: absolute;
    top: 0;
    left: 0;
  }

  .chart-stage {
    @include pin-full;
  }

  .vis-measure-label {
    position: absolute;
    left: 6px;
    top: 12px;
  }

  .hover-guide {
    @include css-variable(stroke, background-brand-light);
    stroke-width: 1;
  }

  &:nth-child(n+2) { // same as not(first-child)
    .highlighter .frame {
      border-top: 0;
    }
  }
}
